            team5: '',
            team6: '',
            team7: '',
            team8: '',
            team9: '',
            team10: '',
            team11: '',
            team12: '',
            team13: '',
            team14: '',
            team15: '',
            team16: '',
            quaterfinal1: '',
            quaterfinal2: '',
            quaterfinal3: '',
            quaterfinal4: '',
            quaterfinal5: '',
            quaterfinal6: '',
            quaterfinal7: '',
            quaterfinal8: '',
            semifinal1: '',
            semifinal2: '',
            semifinal3: '',
            semifinal4: '',
            final1: '',
            final2: '',
            final: ''
        }
    },
    methods:
    {
        QUATERFINAL1(event) {
            if (event.target.value == 1) this.quaterfinal1 = this.team1
            else this.quaterfinal1 = this.team2
        },
        QUATERFINAL2(event) {
            if (event.target.value == 1) this.quaterfinal2 = this.team3
            else this.quaterfinal2 = this.team4
        },
        QUATERFINAL3(event) {
            if (event.target.value == 1) this.quaterfinal3 = this.team5
            else this.quaterfinal3 = this.team6
        },
        QUATERFINAL4(event) {
            if (event.target.value == 1) this.quaterfinal4 = this.team7
            else this.quaterfinal4 = this.team8
        },
        QUATERFINAL5(event) {
            if (event.target.value == 1) this.quaterfinal5 = this.team9
            else this.quaterfinal5 = this.team10
        },
        QUATERFINAL6(event) {
            if (event.target.value == 1) this.quaterfinal6 = this.team11
            else this.quaterfinal6 = this.team12
        },
        QUATERFINAL7(event) {
            if (event.target.value == 1) this.quaterfinal7 = this.team13
            else this.quaterfinal7 = this.team14
        },
        QUATERFINAL8(event) {
            if (event.target.value == 1) this.quaterfinal8 = this.team15
            else this.quaterfinal8 = this.team16
        },
        SEMIFINAL1(event) {
            if (event.target.value == 1) this.semifinal1 = this.quaterfinal1
            else this.semifinal1 = this.quaterfinal2
        },
        SEMIFINAL2(event) {
            if (event.target.value == 1) this.semifinal2 = this.quaterfinal3
            else this.semifinal2 = this.quaterfinal4
        },
        SEMIFINAL3(event) {
            if (event.target.value == 1) this.semifinal3 = this.quaterfinal5
            else this.semifinal3 = this.quaterfinal6
        },
        SEMIFINAL4(event) {
            if (event.target.value == 1) this.semifinal4 = this.quaterfinal7
            else this.semifinal4 = this.quaterfinal8
        },
        FINAL1(event) {
            if (event.target.value == 1) this.final1 = this.semifinal1
            else this.final1 = this.semifinal2
        },
        FINAL2(event) {
            if (event.target.value == 1) this.final2 = this.semifinal3
            else this.final2 = this.semifinal4
        },
        FINAL(event) {
            if (event.target.value == 1) this.final = this.final1
            else this.final = this.final2
        }
    }
}
</script>
