      semifinal4: '',
      final1: '',
      final2: '',
      final: ''
    }
  },
  methods: 
  {
    SEMIFINAL1(event) {
        if (event.target.value == 1) this.semifinal1 = this.team1
        else this.semifinal1 = this.team2
    },
    SEMIFINAL2(event) {
        if (event.target.value == 1) this.semifinal2 = this.team3
        else this.semifinal2 = this.team4
    },
    SEMIFINAL3(event) {
        if (event.target.value == 1) this.semifinal3 = this.team5
        else this.semifinal3 = this.team6
    },
    SEMIFINAL4(event) {
        if (event.target.value == 1) this.semifinal4 = this.team7
        else this.semifinal4 = this.team8
    },
    FINAL1(event) {
        if (event.target.value == 1) this.final1 = this.semifinal1
        else this.final1 = this.semifinal2
    },
    FINAL2(event) {
        if (event.target.value == 1) this.final2 = this.semifinal3
        else this.final2 = this.semifinal4
    },
    FINAL(event) {
      if (event.target.value == 1) this.final = this.final1;
      else this.final = this.final2;
    }
  }
}
